.container {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow-y:auto;
}

.fraction_wrapper {
  width: fit-content;
}


@media screen and (max-width: 800px) {
  .container{
    gap: 0;
  }
}


