.container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  overflow: auto;
  padding: 50px 0;
}

@media screen and (max-width: 1080px) {
  .container {
    padding: 20px 0;
    gap: 20px;
  }
}
